function SoundManager (options) {
    var that = this;
    this.preloadSequence = [];
    this.loadedAudios = [];
    this.maxConnections = options && options.maxConnections ? options.maxConnections : 5;
    this.concurrentLimitLoading = null; //  used to preload audio
    this.monitor = document.getElementById('log');

    var counter = 0;
    options.loadingBtn.addEventListener('touchstart', function(event) {
        that.console('loading start');

        that.concurrentLimitLoading = function () {
            if (that.preloadSequence.length > 0) {
                var concurrent = 0;

                for (var i = 0; i < that.preloadSequence.length; i++) {
                    if (that.preloadSequence[i].requestedLoad == true) {
                        concurrent += 1;
                    }
                }

                //  if not empty and concurrent less than max connections
                if (that.preloadSequence.length > 0 && concurrent < that.maxConnections) {
                    for (var z = 0; z < that.maxConnections; z++) {
                        //  remainder concurrent amount needs to load
                        if (z > concurrent - 1 && z < that.preloadSequence.length) {
                            var audio = that.preloadSequence[z];
                            audio.load();
                            audio.requestedLoad  = true;

                            audio.addEventListener('canplaythrough', function () {
                                console.log('audio ', this.id, ' has loaded.');
                                that.console(this.id + '123');
                                that.loadedAudios.push(this);

                                //  remove from preload sequence
                                for (var x = 0; x < that.preloadSequence.length; x++) {
                                    if (that.preloadSequence[x].id == this.id) {
                                        that.preloadSequence.splice(x, 1);
                                    }
                                }

                                //  update concurrentLimitLoading
                                that.concurrentLimitLoading();
                            }, false);
                        }
                    }
                }
            }
        };

        that.concurrentLimitLoading();
    });
}

SoundManager.prototype.preload = function (assets) {
    var that = this;

    for (var i = 0; i < assets.length; i++) {
        var audio = document.createElement('audio');
        audio.src = assets[i].src;
        audio.id = assets[i].id;
        audio.notForAndriod = assets[i].notForAndriod;
        audio.loop = assets[i].loop || false;
        document.body.appendChild(audio);

        that.preloadSequence.push(audio);
    }

    //  update concurrentLimitLoading
    if (that.concurrentLimitLoading) that.concurrentLimitLoading();
};

SoundManager.prototype.play = function (id) {
    var that = this;
    var ua = window.navigator.userAgent;
    var isAndroid = false;

    if(!!ua.match(/Safari/) && (matched = ua.match(/Android[\s\/]([\d\.]+)/))) { isAndroid = true; }

    for (var i = 0; i < that.loadedAudios.length; i++) {
        var audio = that.loadedAudios[i];

        if (id == that.loadedAudios[i].id) {
            if (isAndroid == false) {
                audio.play();
                that.console(audio.id + ' is playing.');

                if (audio.loop) {
                    audio.onended = function () {
                        this.currentTime = 0;
                        this.play();
                    }
                }
            } else {
                //  play not android audio.
                if (!that.loadedAudios[i].notForAndriod) {
                    audio.play();

                    if (audio.loop) {
                        audio.onended = function () {
                            this.currentTime = 0;
                            this.play();
                        }
                    }
                }
            }

            break;
        }
    }
};

SoundManager.prototype.getAudioAmounts = function () {
    return this.loadedAudios.length;
};

SoundManager.prototype.console = function (txt) {
  if (this.monitor) {
      this.monitor.innerHTML = '<br/>' + txt;
  }
};

