function progressHandler(p) {
    var progress = parseInt(p.loaded*100);

    //  if loaded once
    if (progress >= 100 && !isPart1Loaded) {
        isPart1Loaded = true;
        addBg($('.loading_mark'), 'loading_mark');
        addBg($('.loading_carot'), 'loading_carot');
    }
    //  show the second assets process (main assets)
    else if (isPart1Loaded && isPart2Loaded == false) {
        try {
            var val = progress;

            var $circle = $('#svg #bar');

            if (isNaN(val)) {
                val = 100;
            }
            else{
                var r = $circle.attr('r');
                var c = Math.PI*(r*2);

                if (val < 0) { val = 0;}
                if (val > 100) { val = 100;}

                var pct = ((100-val)/100)*c;

                $circle.css({ strokeDashoffset: pct});

                $('#cont').attr('data-pct',val);
            }

            //  play first scene
            if (progress >= 100) {
                isPart2Loaded = true;

                setTimeout(function () {
                    $('#cont').addClass('end');
                }, 1000);

                /** main entry */
                $('#cont').on('touchstart', startScene01);
            }
        } catch (e) {
            console.log('loading progress error: ', e);
        }
    }
}

