var isAndroid = navigator.userAgent.match(/Mobile\s+Safari/);
var sceneIndex = [1,2,3,4,5,6,7,12,13,14,15,16,17];

var scenePlayTimer = null;
function scenePlay(sceneObj, curFrame, endFrame, carotObj, carotClasName, process, callback, delay) {
    var scene = document.getElementById(sceneObj);
    scene.className += ' active';
    console.log(sceneObj);

    if (sceneObj != 'scene08') {
        for (var i = 0; i < sceneIndex.length; i++) {
            var id = sceneIndex[i] < 10 ? '0' + sceneIndex[i] : sceneIndex[i];

            if (id != sceneObj.substr(5)) {
                document.getElementById('scene' + id).className = document.getElementById('scene' + id).className.replace(/active/g, '');
            }
        }
    }

    if (process) { process(); }

    //  play frames
    clearInterval(scenePlayTimer);

    scenePlayTimer = setInterval(function () {
        if (curFrame > endFrame ) {
            clearInterval(scenePlayTimer);
            //  go to next scene
            if (callback && DEBUG == false) {
                setTimeout(function () {
                    callback();
                }, delay || 1200);
            }
        } else {
            playFrames(carotObj, carotClasName, curFrame++);
        }
    }, 1000/25);
}

function fixedZero (n) { return n < 10 ? '0' + n : n; }